const createSearchParams = (params)=>{
    const query = new URLSearchParams();
    Object.entries(params).forEach(([key, value])=>{
        if (typeof value === "string" || typeof value === "number") {
            query.append(key, value.toString());
        } else if (Array.isArray(value)) {
            value.forEach((valueInner)=>query.append(key, valueInner));
        }
    });
    return query;
};
const fetchHelper = async (endpoint, options = {})=>{
    // Always default to `Accept: application/json`
    let headers = {
        Accept: "application/json",
        ...options.headers
    };
    if (typeof options.method === "string" && options.method.toLowerCase() === "post") {
        // If it's a POST method, default to `Content-Type: application/json` for the body
        headers = {
            "Content-Type": "application/json",
            ...headers
        };
    }
    const response = await fetch(endpoint, {
        mode: "cors",
        ...options,
        headers
    });
    let data = {};
    try {
        // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
        data = await response.json();
    } catch (err) {
    // Some endpoints just return a status code and no JSON body data.
    }
    if (!response.ok) {
        throw new Error(data.error || `Something went wrong calling \`${endpoint}\``);
    }
    return data;
};
let globalBaseUrl = "https://play.charisma.ai";
export const getGlobalBaseUrl = ()=>globalBaseUrl;
