export async function setMemory(token, memoryRecallValueOrMemories, saveValueOrApiOptions, apiOptions) {
    let resolvedApiOptions = apiOptions;
    let memories = [];
    if (Array.isArray(memoryRecallValueOrMemories)) {
        memories = memoryRecallValueOrMemories;
        resolvedApiOptions = saveValueOrApiOptions;
    } else {
        memories = [
            {
                recallValue: memoryRecallValueOrMemories,
                saveValue: saveValueOrApiOptions
            }
        ];
    }
    await fetchHelper(`${resolvedApiOptions?.baseUrl || globalBaseUrl}/play/set-memory`, {
        body: JSON.stringify({
            memories
        }),
        headers: {
            Authorization: `Bearer ${token}`
        },
        method: "POST"
    });
}
